import React, { Component } from 'react';

export default class header extends Component {

  state = {
    data: []
}

  componentDidMount() {
    const headers = {
      headers : {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
        }
      }
    // https://gh-pinned-repos-5l2i19um3.vercel.app/?username=veromoreno
    return fetch('https://api.github.com/users/jorgemejia/repos', headers)
    .then((res) => res.json())
    .then(githubData => {
        this.setState({ data: githubData });
    })
    .catch((error) => {
      console.error('error', error);
    });
  }

  render() {
    return (
      <section id="github-works" className="s-testimonials target-section">
        <div className="row section-intro">
          <div className="col-full">
          <div className="col-full">
              <h3>Github</h3>
              <h1>See my last Projects.</h1>
            </div>
      <ul className="githubList">
        {this.state.data.slice(6, 12).map((data) => (
          <a rel="noreferrer" target="_blank" href={data.html_url}>
              <li className="githubPinned" key={data.id}>
                <div className="d-flex">
                  <div className="pinned-item">
                <p>
                  <svg aria-hidden="true" height="16" viewBox="0 0 16 16" version="1.1" width="16" data-view-component="true" class="octicon octicon-repo mr-1 color-fg-muted">
                    <path fill-rule="evenodd" d="M2 2.5A2.5 2.5 0 014.5 0h8.75a.75.75 0 01.75.75v12.5a.75.75 0 01-.75.75h-2.5a.75.75 0 110-1.5h1.75v-2h-8a1 1 0 00-.714 1.7.75.75 0 01-1.072 1.05A2.495 2.495 0 012 11.5v-9zm10.5-1V9h-8c-.356 0-.694.074-1 .208V2.5a1 1 0 011-1h8zM5 12.25v3.25a.25.25 0 00.4.2l1.45-1.087a.25.25 0 01.3 0L8.6 15.7a.25.25 0 00.4-.2v-3.25a.25.25 0 00-.25-.25h-3.5a.25.25 0 00-.25.25z"></path>
                  </svg>{data.name}</p>
                <p>{data.description}</p>
                <p>{data.language}</p>
                </div>
                </div>
              </li>
            </a>
            ))}
        </ul>
        <a className="btn btn--primary" href="https://github.com/jorgemejia" rel="noreferrer" target="_blank"><i className="im im-github" aria-hidden="true" />Follow me on Github</a>
        </div></div>
        </section>
    )
  }
}
